// Server-side API route to proxy Mistral streaming requests and avoid CORS
import { NextResponse } from 'next/server';

const MISTRAL_API_KEY = process.env.MISTRAL_API_KEY || "S2nqvZQmrdaRwhNhgcMT3M7uyHcjAK5D";

export async function POST(request: Request) {
  try {
    const { prompt } = await request.json();
    const response = await fetch('https://api.mistral.com/v1/complete', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${MISTRAL_API_KEY}`
      },
      body: JSON.stringify({ model: 'mistral-small', inputs: prompt, stream: true })
    });

    if (!response.ok || !response.body) {
      return NextResponse.json({ error: `Mistral API returned ${response.status}` }, { status: 500 });
    }

    return new NextResponse(response.body, {
      status: 200,
      headers: { 'Content-Type': 'text/event-stream' }
    });
  } catch (err) {
    console.error('Mistral proxy error:', err);
    return NextResponse.json({ error: 'Internal server error' }, { status: 500 });
  }
}
